import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Row, Col, Form, Button, Icon, Empty  } from 'antd';
import actions from '../../redux/claim/actions';
import FormBillingInfoDetails from './billingInfoDetails';

const SvgButtonComponent = props => (
  <svg width={'100%'} height={52}>
    <path
      d="M3.494 0h115.811l23.11 27.246L119.305 52H3.494C1.564 52 0 48.866 0 45V7c0-3.866 1.564-7 3.494-7z"
      fill="#f17d27"
    />
    <text
      transform="translate(61.415 33)"
      fill="#fff"
      fontSize={20}
      fontFamily="SegoeUI-Semibold, Segoe UI"
      fontWeight={600}
      letterSpacing=".02em"
    >
      <tspan x={-37.191} y={0}>
        {'SUBMIT'}
      </tspan>
    </text>
  </svg>
);

class BillingInfoForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
    };
    this.clickChild = {};
  }

  componentDidMount() {
    const { exchangeRateInit } = this.props;
    exchangeRateInit();
  }
  handleClick = e => {
    console.log(e.target);
  };

  render() {
    const { exchangeRate, visitsData, BenefitsLabel } = this.props.claim;
    const { insertBillingInfo } = this.props

    //https://ant.design/components/form/?locale=en-US#components-form-demo-form-in-modal
    //Store Form Data into Upper Component

    return (
      <Col offset={2} span={16}>
        {(visitsData == null) || visitsData < 1 ?  <Empty description={<span>No Visit entered</span>}/>:
          visitsData.map(visit => (
              <FormBillingInfoDetails
                key={visit.id}
                visit={visit}
                exchangeRate={exchangeRate}
                BenefitsLabel={BenefitsLabel}
                setClick={click => {
                  this.clickChild[visit.id] = click;
                }}
              />
            ))
          }

          <Row style={{paddingTop:50, paddingBottom:20}} type="flex" justify="end">
            <Button type="link" htmlType="submit"
            onClick={() => {
              const tasks = Object.values(this.clickChild).map(fn => {

                return fn();
              });

              Promise.all(tasks)
              .then(function(values) {

                insertBillingInfo(values);
              })
              .catch(err => console.log(err));
              // Object.keys(this.clickChild).forEach(async function(key) {
              //    // code
              //  });


            }}>
                <Icon component={SvgButtonComponent} />
            </Button>
          </Row>
          <Row>
            <svg width={949} height={5}>
              <path
                fill="none"
                stroke="#f17d27"
                strokeWidth={5}
                d="M0 2.5h949"
              />
            </svg>
          </Row>
      </Col>
    );
  }
}

const WrappedBillingInfoForm = Form.create({ name: 'BillingInfoForm' })(
  BillingInfoForm
);

export default connect(
  state => {

    return { claim: state.Claim };
  },
  actions
)(WrappedBillingInfoForm);
