import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Row, Col, Form, Upload, Select,
Collapse, Divider, InputNumber} from 'antd';
import actions from '../../redux/claim/actions';
import UploadImg from './UploadButton.png';

const { exchangeRateInit } = actions;



const TitleSvgComponent = props => (
  <svg {...props} width={236} height={33}>
    <text
      data-name="Visit_1(12 Jan 2020):"
      transform="translate(0 27)"
      fill="#f17d27"
      fontSize={25}
      fontFamily="SegoeUI-Bold, Segoe UI"
      fontWeight={700}
    >
      <tspan x={0} y={0}>
        Visit {props.name}
      </tspan>
      <tspan y={0} fontFamily="SegoeUI, Segoe UI" fontWeight={400}>
        {props.date? props.date:null}
      </tspan>
    </text>
  </svg>
)



class BillingInfoForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currency:null,
      data:{},
      fileList: [],
    }
    this.getAlert = this.getAlert.bind(this);
  }

  componentDidMount() {

    const { exchangeRateInit } = this.props;
    exchangeRateInit()
    this.props.setClick(this.getAlert);
  }


  getAlert() {
    return new Promise(
        (resolve, reject) => {
          this.props.form.validateFieldsAndScroll((err, values) => {

            if (!err) {
               var visitId = this.props.visit.id;
               var fileList = this.state.fileList;
               var currency = this.state.currency
               let billingInfos = []
              Object.keys(values).forEach(key => {
                if(values[key] === undefined)
                  delete values[key];
                else {

                  billingInfos.push({value:values[key],billingCat:key.split('_')[0],billingSubCat:key.split('_')[1]})
                }

              })


              resolve({visitId, currency, fileList, billingInfos});
            } else {
              reject({visitId, err:'not completed'});
            }
          }
        )
      }
    )
  }

  onSelectChange = (value, visitid, option) => {
    this.setState({
        currency: {billingCurrency:option.props.value, currencyDate:option.props.date, usdper:option.props.usdper}
    })
  };


  handleConfirmBlur = e => {

    const { value, name } = e.target;
    var newData = this.state.data;

    newData[name] = value
    this.setState({ data: newData });

  };

  handleSubmit = e => {
    e.preventDefault();
    console.log('handleSubmit');

    this.props.form.validateFieldsAndScroll((err, values) => {

      Object.keys(values).forEach(key => values[key] === undefined && delete values[key])

      console.log(values)
      console.log(this.props.visit.visitId)

    })
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    const { visit, exchangeRate, BenefitsLabel } = this.props;
    const { currency, data} = this.state;

    const rowBlockstyling = {
      marginBottom: '5%'
    };
    const customPanelStyle = {
      background: '#0F305E',
      paddingBottom: 24,
      border: 0,
      overflow: 'hidden',
    };
    return (
      <Form style={rowBlockstyling} onSubmit={this.handleSubmit}>
        <Row type="flex" justify="space-between" name="currency" style={rowBlockstyling}>
          <Col>
            <TitleSvgComponent
            date={visit.dateOfAdmissionVisit ==null?
              null:visit.dateOfAdmissionVisit.format('L')} />
          </Col>
          <Col span={8}>
            <svg width={200} height={24}>
              <text transform="translate(0 19)" fill="#fff">
                <tspan x={0} y={0}>
                  {'Billing Currency:'} {this.state.currency? this.state.currency.usdper:null}
                </tspan>
              </text>
            </svg>
            <Select style={{ width: '100%' }}  placeholder='Select or enter...' onChange={(e, option) => this.onSelectChange(e,visit.visitId, option)} visitId={visit.visitId}>
              {exchangeRate != null?exchangeRate.map(currency => (
                <Select.Option value={currency.code} key = {currency.code} date = {currency.date} perusd = {currency.perUSD} usdper = {currency.USDper}>
                  {currency.name} - {currency.code}
                </Select.Option>
              )) : null}
            </Select>
          </Col>
        </Row>
        <Row name="data">
          <Collapse bordered={false}>
            {BenefitsLabel.map(category =>
              <Collapse.Panel showArrow= {false} header={(
                <Row>
                  <Col span={12}>
                  {category.title}
                  </Col>
                  <Col span={6}>
                    {currency == null?null:currency.billingCurrency}
                  </Col>
                  <Col span={6}>
                  USD
                  </Col>
                </Row>
              )} key={category.CategroiesId} style={customPanelStyle}>
                {category.subCategories.map(subcategory =>
                  <div key={subcategory.subCategoriesId}>
                    <Row type="flex" style={{paddingTop: 10}} align="middle">
                      <Col span={12}>
                        {subcategory.title}
                      </Col>
                      <Col span={3}>
                      <Form.Item style={{height:20}}>
                        {getFieldDecorator((category.CategroiesId+'_'+subcategory.subCategoriesId), {
                          rules: [],
                        })(
                          <InputNumber name={category.CategroiesId+'_'+subcategory.subCategoriesId} onBlur={this.handleConfirmBlur}/>
                        )}
                      </Form.Item>
                      </Col>
                      <Col span={6} offset={3}>
                       {currency==null?null:String(parseFloat(data[category.CategroiesId+'_'+subcategory.subCategoriesId])*parseFloat(currency.usdper))}
                      </Col>
                    </Row>
                    <Divider />
                  </div>
                )}
              </Collapse.Panel>
            )}
          </Collapse>
        </Row>
        <Row style={{ width: 300 }}>
          <Upload onRemove= {file => {
                  this.setState(state => {
                    const index = state.fileList.indexOf(file);
                    const newFileList = state.fileList.slice();
                    newFileList.splice(index, 1);
                    return {
                      fileList: newFileList,
                    };
                  });
                }}
                beforeUpload= {file => {
                  this.setState(state => ({
                    fileList: [...state.fileList,file],
                  }));
                  return false;
                }} listType= {'text'} className={'upload-list-inline'} >
            <img src={UploadImg} alt="UploadImg" />
          </Upload>
        </Row>
      </Form>
    );
  }
}


const WrappedBillingInfoForm = Form.create({ name: 'BillingInfoForm' })(BillingInfoForm);

export default connect(
  state => {
    return {}
  },
  { exchangeRateInit}
)(WrappedBillingInfoForm);
