import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Row, Col,
Form, Input , Upload, Select, DatePicker,
Checkbox, InputNumber, Button, Icon  } from 'antd';
import jwtDecode from 'jwt-decode';
import UploadImg from './UploadButton.png';
import claimAction from '../../redux/claim/actions';
import currencyData from './currencyData';
import UploadStyleWrapper from './upload.style';

const { Option } = Select;
const { addClaimInfo } = claimAction;

const SvgButtonComponent = props => (
  <svg width={142.415} height={52}>
    <path
      d="M3.494 0h115.811l23.11 27.246L119.305 52H3.494C1.564 52 0 48.866 0 45V7c0-3.866 1.564-7 3.494-7z"
      fill="#f17d27"
    />
    <text
      transform="translate(61.415 33)"
      fill="#fff"
      fontSize={20}
      fontFamily="SegoeUI-Semibold, Segoe UI"
      fontWeight={600}
      letterSpacing=".02em"
    >
      <tspan x={-37.191} y={0}>
        {'SUBMIT'}
      </tspan>
    </text>
  </svg>
)

class PreviewWithLayout extends Component {
  constructor(props) {
    super(props);
    this.state = {
      visits: [],
      claimFileList: [],
    }
  }
//https://malcoded.com/posts/react-file-upload/
  handleSubmit = e => {
    e.preventDefault();

    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        var visitsdata = [];
      //https://clubmate.fi/javascript-manipulating-objects-with-object-keys/
      //https://stackoverflow.com/questions/38750705/filter-object-properties-by-key-in-es6
        //fiter or visit data to visits except files
        var visits = Object.keys(values).filter(key=> key.startsWith('visit')).reduce((obj, key) => {
          obj[key] = values[key];
          return obj;
        }, {});

        for (var i = 1, len = this.state.visits.length+1; i < len; i++) {
          visitsdata.push(
            {
              visitNumber:i,
              hospitalOrClinicName:visits['visit'+i+'hospitalorclinicname'],
              hospitalOrClinicCountryrl:visits['visit'+i+'hospitalorclinicurl'],
              hospitalOrclinicEmail:visits['visit'+i+'hospitalorclinicemail'],
              MedicalDiagnosis:visits['visit'+i+'medicaldiagnosis'],
              dateOfAdmissionVisit:visits['visit'+i+'dateofadmissionvisit'],
              hospitalOrClinicCountry:visits['visit'+i+'hospitalorcliniccountry'],
              doctorName:visits['visit'+i+'doctorname'],

            }
          )
        }
        var data = Object.keys(values).filter(key=> !key.startsWith('visit')).reduce((obj, key) => {
          obj[key] = values[key];
          return obj;
        }, {});

        const profile = jwtDecode(this.props.idToken);
        data.createdBy = profile.id

        this.props.addClaimInfo({claimData:data, visitsData:visitsdata, claimFileList:this.state.claimFileList});
      }
    });
  };
//https://www.robinwieruch.de/react-state-array-add-update-remove
  onAdd = e => {
    this.setState(state => {
      const visits = state.visits.concat({visitNumber:(this.state.visits.length+1)});
      return {
        visits
      };
    });

  };

  onDelete = i => {
    //this is the way using DOM
    //console.log(e.currentTarget.getAttribute("name"))

    this.setState(state => {
      const visits = state.visits;
      visits.pop()
      return {
        visits,
      };
    });
  };

  render() {

    const { getFieldDecorator } = this.props.form;
    const formItemstyling = {
      marginRight: '25px'
    };
    const formItemFirstColstyling = {
      md: { span: 10 },
      xs: { span: 22 }
    };
    const formItemSecondColstyling = {
      md: { span: 8 },
      xs: { span: 22 }
    };
    const formItemThirdColstyling = {
      md: { span: 4 },
      xs: { span: 22 }
    };
    const rowBlockstyling = {
      marginBottom: '5%'
    };

    return (
      <Form onSubmit={this.handleSubmit}>
        <Col offset={2} md={14} sm={22} xs={22}>

          <Row name = "policyDetail">
            <Row name = "policyTitle">
              <svg xmlns="http://www.w3.org/2000/svg" width="218" height="40" viewBox="0 0 218 40">
                <text id="_1._Policy_Details" transform="translate(0 32)" fill="#fff" fontSize="30" fontFamily="SegoeUI-Semibold, Segoe UI" fontWeight="600"><tspan x="0" y="0">1. Policy Details</tspan></text>
              </svg>
            </Row>
            <Row name = "policyholderForm" style={rowBlockstyling}>
              <Col {...formItemFirstColstyling}>
                <Form.Item style={formItemstyling} label={<svg xmlns="http://www.w3.org/2000/svg" width="192" height="24" viewBox="0 0 192 24">
                  <text id="Policyholder_First_Name:" transform="translate(0 19)" fill="#fff" fontSize="18" fontFamily="SegoeUI, Segoe UI"><tspan x="0" y="0">Policyholder First Name:</tspan></text>
                </svg>}>
                  {getFieldDecorator('policyFirstName', {
                    rules: [
                      {
                        required: true,
                        message: 'Please input Policyholder First Name',
                      },
                    ],
                  })(
                    <Input
                      placeholder="Daniel"
                    />,
                  )}
                </Form.Item>
                <Form.Item style={formItemstyling} label={<svg xmlns="http://www.w3.org/2000/svg" width="192" height="24" viewBox="0 0 192 24">
                  <text id="Policyholder_Last_Name:" transform="translate(0 19)" fill="#fff" fontSize="18" fontFamily="SegoeUI, Segoe UI"><tspan x="0" y="0">Policyholder Last Name:</tspan></text>
                </svg>}>
                  {getFieldDecorator('policyLastName', {
                    rules: [
                      {
                        required: true,
                        message: 'Please input Policyholder Last Name',
                      },
                    ],
                  })(
                    <Input
                      placeholder="Smith"
                    />,
                  )}
                </Form.Item>
                <Form.Item style={formItemstyling} label={<svg xmlns="http://www.w3.org/2000/svg" width="192" height="24" viewBox="0 0 192 24">
                  <text id="Policyholder_Email:" transform="translate(0 19)" fill="#fff" fontSize="18" fontFamily="SegoeUI, Segoe UI"><tspan x="0" y="0">Policyholder Email:</tspan></text>
                </svg>}>
                  {getFieldDecorator('policyEmail', {
                    rules: [],
                  })(
                    <Input
                    />,
                  )}
                </Form.Item>
              </Col>
              <Col {...formItemSecondColstyling}>
                <Form.Item style={formItemstyling} label={<svg xmlns="http://www.w3.org/2000/svg" width="192" height="24" viewBox="0 0 192 24">
                  <text id="Policy_number:" transform="translate(0 19)" fill="#fff" fontSize="18" fontFamily="SegoeUI, Segoe UI"><tspan x="0" y="0">Policy Number:</tspan></text>
                </svg>}>
                  {getFieldDecorator('policyNumber', {
                    rules: [
                      {
                        required: true,
                        message: 'Please input Policy Number',
                      },
                    ],
                  })(
                    <Input.Search placeholder="RIH/YYYY/XX/11111111" onSearch={value => {
                      if (['TLS', 'LI', 'MM', 'ST', 'CO', 'FC'].includes(value.split("/")[2])) {
                        this.props.form.setFieldsValue({policyType:value.split("/")[2]})
                      }
                    }} enterButton />,
                  )}
                </Form.Item>
                <Form.Item style={formItemstyling} label={<svg xmlns="http://www.w3.org/2000/svg" width="192" height="24" viewBox="0 0 192 24">
                  <text id="Policyholder_Last_Name:" transform="translate(0 19)" fill="#fff" fontSize="18" fontFamily="SegoeUI, Segoe UI"><tspan x="0" y="0">Policyholder Type:</tspan></text>
                </svg>}>
                  {getFieldDecorator('policyType', {
                    rules: [],
                  })(
                    <Select style={{ width: '100%' }} placeholder='Auto populate or select...'>
                      <Option value="TLS">Thailand Long Stay</Option>
                      <Option value="LI">Life Insurance</Option>
                      <Option value="MM">Major Medical</Option>
                      <Option value="ST">Standard</Option>
                      <Option value="CO">Comprehensive</Option>
                      <Option value="FC">Fully Comprehensive</Option>
                    </Select>
                  )}
                </Form.Item>
              </Col>
              <Col {...formItemThirdColstyling}>
                <Form.Item style={formItemstyling} label={<svg xmlns="http://www.w3.org/2000/svg" width="192" height="24" viewBox="0 0 192 24">
                  <text id="Policyholder_VIP:" transform="translate(0 19)" fill="#fff" fontSize="18" fontFamily="SegoeUI, Segoe UI"><tspan x="0" y="0">VIP</tspan></text>
                </svg>}>
                  {getFieldDecorator('policyVip', {
                    rules: [],
                  })(
                    <Checkbox>
                    </Checkbox>
                  )}
                </Form.Item>
                <Form.Item style={formItemstyling} label={<svg xmlns="http://www.w3.org/2000/svg" width="192" height="24" viewBox="0 0 192 24">
                  <text id="Policyholder_VIP:" transform="translate(0 19)" fill="#fff" fontSize="18" fontFamily="SegoeUI, Segoe UI"><tspan x="0" y="0">Group Policy</tspan></text>
                </svg>}>
                  {getFieldDecorator('policyGroupPolicy', {
                    rules: [],
                  })(
                    <Checkbox>
                    </Checkbox>
                  )}
                </Form.Item>
              </Col>
            </Row>
            <Row name = "patientForm" style={rowBlockstyling}>
              <Col {...formItemFirstColstyling}>
                <Form.Item style={formItemstyling} label={<svg xmlns="http://www.w3.org/2000/svg" width="192" height="24" viewBox="0 0 192 24">
                  <text id="Policyholder_First_Name:"  transform="translate(0 19)" fill="#fff" fontSize="18" fontFamily="SegoeUI, Segoe UI"><tspan x="0" y="0">Patient First Name:</tspan></text>
                </svg>}>
                  {getFieldDecorator('patientFirstName', {
                    rules: [],
                  })(
                    <Input
                    />,
                  )}
                </Form.Item>
                <Form.Item style={formItemstyling} label={<svg xmlns="http://www.w3.org/2000/svg" width="192" height="24" viewBox="0 0 192 24">
                  <text id="Policyholder_Last_Name:" transform="translate(0 19)" fill="#fff" fontSize="18" fontFamily="SegoeUI, Segoe UI"><tspan x="0" y="0">Patient Last Name:</tspan></text>
                </svg>}>
                  {getFieldDecorator('patientLastName', {
                    rules: [],
                  })(
                    <Input
                    />,
                  )}
                </Form.Item>
              </Col>
              <Col {...formItemSecondColstyling}>
                <Form.Item style={formItemstyling} label={<svg xmlns="http://www.w3.org/2000/svg" width="192" height="24" viewBox="0 0 192 24">
                  <text id="Patient_DOB:" transform="translate(0 19)" fill="#fff" fontSize="18" fontFamily="SegoeUI, Segoe UI"><tspan x="0" y="0">Patient DOB:</tspan></text>
                </svg>}>
                  {getFieldDecorator('patientDob', {
                    rules: [],
                  })(
                    <DatePicker style={{width:'100%'}}/>
                  )}
                </Form.Item>
              </Col>
              <Col {...formItemThirdColstyling}>
                <Form.Item style={formItemstyling} label={<svg xmlns="http://www.w3.org/2000/svg" width="192" height="24" viewBox="0 0 192 24">
                  <text id="Patient_Age:" transform="translate(0 19)" fill="#fff" fontSize="18" fontFamily="SegoeUI, Segoe UI"><tspan x="0" y="0">Patient Age</tspan></text>
                </svg>}>
                  {getFieldDecorator('patientAge', {
                    rules: [],
                  })(
                    <InputNumber/>
                  )}
                </Form.Item>
              </Col>
            </Row>
            <Row name = "contactForm" style={rowBlockstyling}>
              <Col {...formItemFirstColstyling}>
                <Form.Item style={formItemstyling} label={<svg xmlns="http://www.w3.org/2000/svg" width="192" height="24" viewBox="0 0 192 24">
                  <text id="Contact_First_Name:" transform="translate(0 19)" fill="#fff" fontSize="18" fontFamily="SegoeUI, Segoe UI"><tspan x="0" y="0">Contact First Name:</tspan></text>
                </svg>}>
                  {getFieldDecorator('contactFirstName', {
                    rules: [],
                  })(
                    <Input
                    />,
                  )}
                </Form.Item>
                <Form.Item style={formItemstyling} label={<svg xmlns="http://www.w3.org/2000/svg" width="192" height="24" viewBox="0 0 192 24">
                  <text id="Contact_Last_Name:" transform="translate(0 19)" fill="#fff" fontSize="18" fontFamily="SegoeUI, Segoe UI"><tspan x="0" y="0">Contact Last Name:</tspan></text>
                </svg>}>
                  {getFieldDecorator('contactLastName', {
                    rules: [],
                  })(
                    <Input
                    />,
                  )}
                </Form.Item>
                <Form.Item style={formItemstyling} label={<svg xmlns="http://www.w3.org/2000/svg" width="192" height="24" viewBox="0 0 192 24">
                  <text id="Contact_Email:" transform="translate(0 19)" fill="#fff" fontSize="18" fontFamily="SegoeUI, Segoe UI"><tspan x="0" y="0">Contact Email:</tspan></text>
                </svg>}>
                  {getFieldDecorator('contactEmail', {
                    rules: [],
                  })(
                    <Input
                    />,
                  )}
                </Form.Item>
                <Form.Item style={formItemstyling} label={<svg xmlns="http://www.w3.org/2000/svg" width="192" height="24" viewBox="0 0 192 24">
                  <text id="Contact_Home_Address:" transform="translate(0 19)" fill="#fff" fontSize="18" fontFamily="SegoeUI, Segoe UI"><tspan x="0" y="0">Contact Home Address:</tspan></text>
                </svg>}>
                  {getFieldDecorator('contactHomeAddress', {
                    rules: [],
                  })(
                    <Input.TextArea rows={4}
                    />,
                  )}
                </Form.Item>
              </Col>
              <Col {...formItemSecondColstyling}>
                <Row>
                  <Col span={8}>
                    <Form.Item style={formItemstyling} label={<svg xmlns="http://www.w3.org/2000/svg" width="192" height="24" viewBox="0 0 192 24">
                      <text id="Contact_Phone_Number:" transform="translate(0 19)" fill="#fff" fontSize="18" fontFamily="SegoeUI, Segoe UI"><tspan x="0" y="0">Contact Phone Number:</tspan></text>
                    </svg>}>
                      {getFieldDecorator('contactPhoneNumberCountryCode', {
                        rules: [],
                      })(
                            <Input placeholder="+000" />

                      )}
                    </Form.Item>
                  </Col>
                  <Col span={16}>
                    <Form.Item style={formItemstyling} label={<svg xmlns="http://www.w3.org/2000/svg" width="192" height="24" viewBox="0 0 192 24">
                    </svg>}>
                      {getFieldDecorator('contactPhoneNumber', {
                        rules: [],
                      })(

                            <Input placeholder="000000000000" />
                      )}
                    </Form.Item>
                  </Col>
                </Row>
                <Form.Item style={formItemstyling} label={<svg xmlns="http://www.w3.org/2000/svg" width="192" height="24" viewBox="0 0 192 24">
                  <text id="Relationship_To_Patient:" transform="translate(0 19)" fill="#fff" fontSize="18" fontFamily="SegoeUI, Segoe UI"><tspan x="0" y="0">Relationship to Patient:</tspan></text>
                </svg>}>
                  {getFieldDecorator('relationshipToPatient', {
                    rules: [],
                  })(
                    <Input />
                  )}
                </Form.Item>
              </Col>
            </Row>
            <Row name = "AccountForm" style={rowBlockstyling}>
              <Col {...formItemFirstColstyling}>
                <Form.Item style={formItemstyling} label={<svg xmlns="http://www.w3.org/2000/svg" width="192" height="24" viewBox="0 0 192 24">
                  <text id="Account_Holders_Name:" transform="translate(0 19)" fill="#fff" fontSize="18" fontFamily="SegoeUI, Segoe UI"><tspan x="0" y="0">Account Holders Name:</tspan></text>
                </svg>}>
                  {getFieldDecorator('accountHoldersName', {
                    rules: [],
                  })(
                    <Input
                    />,
                  )}
                </Form.Item>
                <Form.Item style={formItemstyling} label={<svg xmlns="http://www.w3.org/2000/svg" width="192" height="24" viewBox="0 0 192 24">
                  <text id="Bank_Name:" transform="translate(0 19)" fill="#fff" fontSize="18" fontFamily="SegoeUI, Segoe UI"><tspan x="0" y="0">Bank Name:</tspan></text>
                </svg>}>
                  {getFieldDecorator('bankName', {
                    rules: [],
                  })(
                    <Input
                    />,
                  )}
                </Form.Item>
                <Form.Item style={formItemstyling} label={<svg xmlns="http://www.w3.org/2000/svg" width="192" height="24" viewBox="0 0 192 24">
                  <text id="Bank_Address:" transform="translate(0 19)" fill="#fff" fontSize="18" fontFamily="SegoeUI, Segoe UI"><tspan x="0" y="0">Bank Address:</tspan></text>
                </svg>}>
                  {getFieldDecorator('bankAddress', {
                    rules: [],
                  })(
                    <Input.TextArea rows={6}
                    />,
                  )}
                </Form.Item>
              </Col>
              <Col {...formItemSecondColstyling}>
                <Form.Item style={formItemstyling} label={<svg xmlns="http://www.w3.org/2000/svg" width="192" height="24" viewBox="0 0 192 24">
                  <text id="Reimbusement_Currency:" transform="translate(0 19)" fill="#fff" fontSize="18" fontFamily="SegoeUI, Segoe UI"><tspan x="0" y="0">Reimbusement Currency:</tspan></text>
                </svg>}>
                  {getFieldDecorator('reimbusementCurrency', {
                    rules: [],
                  })(
                    <Select style={{ width: '100%' }}  placeholder='Auto populate or select...'>
                      {currencyData.map(currency => (
                        <Option value={currency.value} key = {currency.value}>{currency.label} - {currency.value}</Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
                <Form.Item style={formItemstyling} label={<svg xmlns="http://www.w3.org/2000/svg" width="192" height="24" viewBox="0 0 192 24">
                  <text id="Bank_Account_Number:" transform="translate(0 19)" fill="#fff" fontSize="18" fontFamily="SegoeUI, Segoe UI"><tspan x="0" y="0">Bank Account Number:</tspan></text>
                </svg>}>
                  {getFieldDecorator('bankAccountNumber', {
                    rules: [],
                  })(
                    <Input placeholder='00000000000000'/>
                  )}
                </Form.Item>
                <Form.Item style={formItemstyling} label={<svg xmlns="http://www.w3.org/2000/svg" width="192" height="24" viewBox="0 0 192 24">
                  <text id="IBAN_Code_Sort_Code:" transform="translate(0 19)" fill="#fff" fontSize="18" fontFamily="SegoeUI, Segoe UI"><tspan x="0" y="0">IBAN Code/Sort Code:</tspan></text>
                </svg>}>
                  {getFieldDecorator('ibanCodeSortCode', {
                    rules: [],
                  })(
                    <Input placeholder='00000000000000'/>
                  )}
                </Form.Item>
                <Form.Item style={formItemstyling} label={<svg xmlns="http://www.w3.org/2000/svg" width="192" height="24" viewBox="0 0 192 24">
                  <text id="SWIFT:" transform="translate(0 19)" fill="#fff" fontSize="18" fontFamily="SegoeUI, Segoe UI"><tspan x="0" y="0">SWIFT:</tspan></text>
                </svg>}>
                  {getFieldDecorator('swift', {
                    rules: [],
                  })(
                    <Input placeholder='00000000'/>
                  )}
                </Form.Item>
              </Col>
            </Row>
            <Row name = "Divider" style={rowBlockstyling}>
              <svg width={948} height={1}>
                <path fill="none" stroke="#fff" d="M0 .5h948" />
              </svg>
            </Row>
          </Row>
          <Row name = "claimDetail">
            <Row name = "claimTitle">
              <Col md={18} sm={22} xs={22}>
                <svg xmlns="http://www.w3.org/2000/svg" width="218" height="40" viewBox="0 0 218 40">
                  <text id="_2._Claim_Details" transform="translate(0 32)" fill="#fff" fontSize="30" fontFamily="SegoeUI-Semibold, Segoe UI" fontWeight="600"><tspan x="0" y="0">2. Claim Details</tspan></text>
                </svg>
              </Col>
              <Col {...formItemThirdColstyling}>
                <Form.Item style={formItemstyling} label={<svg xmlns="http://www.w3.org/2000/svg" width="192" height="24" viewBox="0 0 192 24">
                  <text id="GOP:" transform="translate(0 19)" fill="#fff" fontSize="18" fontFamily="SegoeUI, Segoe UI"><tspan x="0" y="0">GOP</tspan></text>
                </svg>}>
                  {getFieldDecorator('gop', {
                    rules: [],
                  })(
                    <Checkbox>
                    </Checkbox>
                  )}
                </Form.Item>
              </Col>
            </Row>
            <Row name = "causeForm" style={rowBlockstyling}>
              <Col md={18} sm={22} xs={22}>
                <Form.Item style={formItemstyling} label={<svg xmlns="http://www.w3.org/2000/svg" width="192" height="24" viewBox="0 0 192 24">
                  <text id="Cause:" transform="translate(0 19)" fill="#fff" fontSize="18" fontFamily="SegoeUI, Segoe UI"><tspan x="0" y="0">Cause:</tspan></text>
                </svg>}>
                  {getFieldDecorator('cause', {
                    rules: [],
                  })(
                    <Input.TextArea rows={4}
                    />,
                  )}
                </Form.Item>
              </Col>
            </Row>
            {this.state.visits.map((val, i) => {
              let j= i+1
              return (
                <div key={j} name='visitForm' style={rowBlockstyling}>
                  <Row name='visitTitle'>
                    <svg width={60} height={24}>
                      <text
                        transform="translate(0 19)"
                        fill="#f17d27"
                        fontSize={18}
                        fontFamily="SegoeUI-Bold, Segoe UI"
                        fontWeight={700}
                      >
                        <tspan x={0} y={0}>
                          {'Visit '+val.visitNumber}
                        </tspan>
                        <tspan y={0} fontFamily="SegoeUI, Segoe UI" fontWeight={400}>
                          {':'}
                        </tspan>
                      </text>
                    </svg>
                  </Row>
                  <Row name = {"visitForm"+i} style={rowBlockstyling}>
                    <Col {...formItemFirstColstyling}>
                      <Form.Item style={formItemstyling} label={<svg xmlns="http://www.w3.org/2000/svg" width="192" height="24" viewBox="0 0 192 24">
                        <text id="Hospital_or_Clinic_Name:" transform="translate(0 19)" fill="#fff" fontSize="18" fontFamily="SegoeUI, Segoe UI"><tspan x="0" y="0">Hospital or Clinic Name:</tspan></text>
                      </svg>}>
                        {getFieldDecorator('visit'+j+'hospitalorclinicname', {
                          rules: [],
                        })(
                          <Input
                          />,
                        )}
                      </Form.Item>
                      <Form.Item style={formItemstyling} label={<svg xmlns="http://www.w3.org/2000/svg" width="192" height="24" viewBox="0 0 192 24">
                        <text id="Hospital_or_Clinic_Url:" transform="translate(0 19)" fill="#fff" fontSize="18" fontFamily="SegoeUI, Segoe UI"><tspan x="0" y="0">Hospital or Clinic url:</tspan></text>
                      </svg>}>
                        {getFieldDecorator('visit'+j+'hospitalorclinicurl', {
                          rules: [],
                        })(
                          <Input
                          />,
                        )}
                      </Form.Item>
                      <Form.Item style={formItemstyling} label={<svg xmlns="http://www.w3.org/2000/svg" width="192" height="24" viewBox="0 0 192 24">
                        <text id="Hospital_or_Clinic_Email:" transform="translate(0 19)" fill="#fff" fontSize="18" fontFamily="SegoeUI, Segoe UI"><tspan x="0" y="0">Hospital or Clinic Email:</tspan></text>
                      </svg>}>
                        {getFieldDecorator('visit'+j+'hospitalorclinicemail', {
                          rules: [],
                        })(
                          <Input
                          />,
                        )}
                      </Form.Item>
                      <Form.Item style={formItemstyling} label={<svg xmlns="http://www.w3.org/2000/svg" width="192" height="24" viewBox="0 0 192 24">
                        <text id="Medical_Diagnosis:" transform="translate(0 19)" fill="#fff" fontSize="18" fontFamily="SegoeUI, Segoe UI"><tspan x="0" y="0">Medical Diagnosis:</tspan></text>
                      </svg>}>
                        {getFieldDecorator('visit'+j+'medicaldiagnosis', {
                          rules: [],
                        })(
                          <Input
                          />,
                        )}
                      </Form.Item>
                    </Col>
                    <Col {...formItemSecondColstyling}>
                      <Form.Item style={formItemstyling} label={<svg xmlns="http://www.w3.org/2000/svg" width="192" height="24" viewBox="0 0 192 24">
                        <text id="Date_of_Admission_Visit:" transform="translate(0 19)" fill="#fff" fontSize="18" fontFamily="SegoeUI, Segoe UI"><tspan x="0" y="0">Date of Admission/Visit:</tspan></text>
                      </svg>}>
                        {getFieldDecorator('visit'+j+'dateofadmissionvisit', {
                          rules: [],
                        })(
                          <DatePicker style={{width:'100%'}}/>
                        )}
                      </Form.Item>
                      <Form.Item style={formItemstyling} label={<svg xmlns="http://www.w3.org/2000/svg" width="192" height="24" viewBox="0 0 192 24">
                        <text id="Hospital_or_Clinic_Country:" transform="translate(0 19)" fill="#fff" fontSize="18" fontFamily="SegoeUI, Segoe UI"><tspan x="0" y="0">Hospitl or Clinic Country:</tspan></text>
                      </svg>}>
                        {getFieldDecorator('visit'+j+'hospitalorcliniccountry', {
                          rules: [],
                        })(
                          <Select style={{ width: '100%' }} placeholder='Select country...'>
                            <Option value="jack">Jack</Option>
                            <Option value="lucy">Lucy</Option>
                            <Option value="Yiminghe">yiminghe</Option>
                          </Select>
                        )}
                      </Form.Item>
                      <Form.Item style={formItemstyling} label={<svg xmlns="http://www.w3.org/2000/svg" width="192" height="24" viewBox="0 0 192 24">
                        <text id="Doctor Name:" transform="translate(0 19)" fill="#fff" fontSize="18" fontFamily="SegoeUI, Segoe UI"><tspan x="0" y="0">Doctor Name:</tspan></text>
                      </svg>}>
                        {getFieldDecorator('visit'+j+'doctorname', {
                          rules: [],
                        })(
                          <Input/>
                        )}
                      </Form.Item>
                    </Col>
                  </Row>
                  <UploadStyleWrapper name='uploadForm' style={{ width: 300 }}>
                      <Upload onRemove= {file => {
                              this.setState(state => {
                                const index = state.claimFileList.indexOf(file);
                                const newclaimFileList = state.claimFileList.slice();
                                newclaimFileList.splice(index, 1);
                                return {
                                  claimFileList: newclaimFileList,
                                };
                              });
                            }}
                            beforeUpload= {file => {
                              //or put id to claimFileList state variable, not under visits
                
                              file.visitNumber = val.visitNumber
                              this.setState(state => ({
                                claimFileList: [...state.claimFileList, file],
                              }));
                              return false;
                            }} listType= {'picture'} className={'upload-list-inline'}>
                        <img src={UploadImg} alt="UploadImg" />
                      </Upload>
                  </UploadStyleWrapper>
                </div>
              )
            })}
            <Row>
              <Row>
                <svg xmlns="http://www.w3.org/2000/svg" width="192" height="24" viewBox="0 0 192 24">
                  <text id="Visit:" transform="translate(0 19)" fill="#fff" fontSize="18" fontFamily="SegoeUI, Segoe UI"><tspan x="0" y="0">Visit:</tspan></text>
                </svg>
              </Row>
              <Col span={6}>
                <Icon type="plus-square" style={{ fontSize: '18px', color: '#ffffff' }} theme="filled" onClick = {this.onAdd} />
                <span style={{color:'white',font: '18px SegoeUI, Segoe UI'}}>Add</span>
              </Col >
              <Col span={6}>
                <Icon type="minus-square" style={{ fontSize: '18px', color: '#ffffff' }} theme="filled" onClick = {this.onDelete} />
                <span style={{color:'white',font: '18px SegoeUI, Segoe UI'}}>Delete</span>
              </Col>
            </Row>
          </Row>


          <Row style={{paddingTop:50, paddingBottom:20}} type="flex" justify="end">
            <Button type="link" htmlType="submit">
                <Icon component={SvgButtonComponent} />
            </Button>
          </Row>
          <Row>
            <svg width={949} height={5}>
              <path
                fill="none"
                stroke="#f17d27"
                strokeWidth={5}
                d="M0 2.5h949"
              />
            </svg>
          </Row>
        </Col>
      </Form>
    );
  }
}

const WrappedPreviewWithLayoutForm = Form.create({ name: 'REGISTER_Claims_Form' })(PreviewWithLayout);

export default connect(
  state => {
    return {idToken: state.Auth.idToken}
  },
  {addClaimInfo}
)(WrappedPreviewWithLayoutForm);
