import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Row, Form, Upload, Table, Popconfirm, Icon} from 'antd';
import UploadImg from './UploadButton.png';
import notification from '@isomorphic/shared/isomorphic/components/Notification';
import actions from '../../redux/claim/actions';

import {
  EditableFormRow,
  EditableCell
 } from '@isomorphic/shared/isomorphic/components/Tables/CustomizedCells';

const TitleSvgComponent = props => (
  <svg {...props} width={236} height={33}>
    <text
      data-name="Visit_1(12 Jan 2020):"
      transform="translate(0 27)"
      fill="#f17d27"
      fontSize={25}
      fontFamily="SegoeUI-Bold, Segoe UI"
      fontWeight={700}
    >
      <tspan x={0} y={0}>
        Visit {props.name}
      </tspan>
      <tspan y={0} fontFamily="SegoeUI, Segoe UI" fontWeight={400}>
        ({props.date})
      </tspan>
    </text>
  </svg>
)



class DocumentDetailsForm extends Component {
  constructor(props) {
    super(props);
    this.columns = [
      {
        title: 'Documents',
        dataIndex: 'name',
        width: '30%',
        render: (text, record) =>{
          return this.state.dataSource.length >= 1 ? (<a href="/#"><Icon type="paper-clip" />{text}</a>) : null
        },
      },
      {
        title: 'Updated:',
        dataIndex: 'updated',
      },
      {
        title: 'Notes',
        dataIndex: 'notes',
        editable: true,
      },
      {
        title: 'operation',
        dataIndex: 'operation',
        render: (text, record) =>
          this.state.dataSource.length >= 1 ? (
            <Popconfirm title="Sure to delete?" onConfirm={() => this.handleDelete(record.key)}>
              <a href="/#">Delete</a>
            </Popconfirm>
          ) : null,
      },
    ];
    this.state = {

      dataSource: [],
    }

  }
  handleDelete = key => {
    const dataSource = [...this.state.dataSource];
    this.setState({ dataSource: dataSource.filter(item => item.key !== key) });
  };


  handleSave = row => {

    const newData = [...this.state.dataSource];
    const index = newData.findIndex(item => row.key === item.key);
    const item = newData[index];
    newData.splice(index, 1, {
      ...item,
      ...row,
    });
    this.setState({ dataSource: newData });

    this.props.insertDocuments({path:row.key,notes:row.notes})

  };



  render() {

    const { visit } = this.props;

    const { dataSource } = this.state;

    const components = {
      body: {
        row: EditableFormRow,
        cell: EditableCell,
      },
    };
    const columns = this.columns.map(col => {
      if (!col.editable) {
        return col;
      }
      return {
        ...col,
        onCell: record => ({
          record,
          editable: col.editable,
          dataIndex: col.dataIndex,
          title: col.title,
          handleSave: this.handleSave,
        }),
      };
    });

    const rowBlockstyling = {
      marginBottom: '5%'
    };

    return (
      <Form style={rowBlockstyling} onSubmit={this.handleSubmit}>
        <Row type="flex" justify="space-between" name="currency" style={rowBlockstyling}>
          <TitleSvgComponent
          date={visit.dateOfAdmissionVisit ==null?
            null:visit.dateOfAdmissionVisit.format('L')}/>
        </Row>
        <Row name="tabledata" style={rowBlockstyling}>
          <Table bordered = {false}
          components={components}
          rowClassName={() => 'editable-row'}
          dataSource={dataSource}
          columns={columns}
          pagination={false}
          className={"someruame"}
          />
        </Row>
        <Row name="upload" style={{ width: 300 }}>
  
          <Upload action="/api/claim/uploaddocument"
          data={{ visitId:visit.id}}
          showUploadList={false}
          onChange = {(info) => {
            switch (info.file.status) {
              case 'done':
                const newData = {
                  key: info.file.response.path,
                  name: info.file.name,
                  updated: Date.now(),
                  notes: ``,
                  file:info.file,
                  newFile:true
                };
                notification('success', "File uploaded" );
                this.setState({
                  dataSource: [...dataSource, newData],
                });
                break;
              case 0:

                break;
              default:
                break;
            }

          }}>
            <img src={UploadImg} alt="UploadImg"  />
          </Upload>,
        </Row>
      </Form>
    );
  }
}


const WrappedDocumentDetailsForm = Form.create({ name: 'BillingInfoForm' })(DocumentDetailsForm);

export default connect(
  state => {
    return {}
  },
  actions
)(WrappedDocumentDetailsForm);
